.people-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.people-h1 {
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;

  color: #5e7a7d;
}
.people-non-zero {
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  text-align: right;
  color: #e17457;
}
.people-input {
  border-radius: 5px;
  background: #f3f9fa;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 3px;
}
.people-inp {
  border: none;
  outline: none;
  width: 100%;
  text-align: right;
  font-family: "Space Mono";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  /* identical to box height */
  background-color: transparent;
  text-align: right;
  color: #00474b;
}
